.CommentThread{--bgRGB: 23.5875,25.5,27.4125;background-color:rgba(var(--bgRGB), 1);--textRGB: 227.5875,229.5,231.4125;color:rgba(var(--textRGB), 1);top:var(--headerHeight);background-color:rgba(0,0,0,0.9)}.CommentThread .thread-container{max-width:600px;border-radius:8px;-webkit-backdrop-filter:blur(16px);backdrop-filter:blur(16px);background-color:rgba(var(--textRGB), 0.025);box-shadow:0 0 0 1px rgba(var(--textRGB), 0.05)}@supports not ((-webkit-backdrop-filter: blur(16px)) or (backdrop-filter: blur(16px))){.CommentThread .thread-container{--bgRGB: 26,26,26;background-color:rgba(var(--bgRGB), 1);background-color:rgba(var(--bgRGB), 0.8)}}.CommentThread .Comment .bottom-row .button{display:none !important}.CommentThread .reply-input .top-row>img{box-shadow:0 0 0 1px rgba(var(--textRGB), 0.025)}.CommentThread .reply-input>.form{border-radius:8px;background-color:rgba(var(--textRGB), 0.05);box-shadow:0 0 0 1px rgba(var(--textRGB), 0.075)}

