@import '~supergrids.css/src/scss/mixins';

.segment-controls{
	position: relative;
	overflow: hidden;
	>:not(.highlight){
		@include col;
		position: relative;
		cursor: pointer;
	}
	.highlight{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		transition: transform .5s;
	}
}
