@import '../../_variables';

#Sidemenu{
	@include bg-color($gray-0050);
	@include text-color($gray-0750);
	padding-right: calc(100% - var(--sidemenuWidth) + 24px) !important;
	background-image: linear-gradient(to bottom,$gray-0100,$gray-0050);
	&::before{ // TODO: Update design
		content: '';
		display: block;
		position: absolute;
		top: -25vh;
		left: -67.5vh;
		width: 100vh;
		height: 100vh;
		border-radius: 50%;
		box-shadow: inset 0 0 0 20vh white;
		opacity: .1;
		mix-blend-mode: soft-light;
		pointer-events: none;
	}
	+#main-content{
		//border-radius: 8px 0 0 8px;
		transform-origin: right center;
		transition: transform 1s;
		&::before,
		&::after{
			--d: 8px;
			content: '';
			position: absolute;
			top: calc(var(--d) * 2);
			right: 0;
			bottom: calc(var(--d) * 2);
			left: calc(var(--d) * -1);
			width: 8px;
			border-radius: 8px 0 0 8px;
			background-color: rgba(var(--bgRGB),.25);
			transition: right 1s;
		}
		&::before{
			--d: 16px;
			opacity: .25;
		}
	}
	&.active+#main-content{
		border-radius: 8px;
		transform: translateX(calc(var(--sidemenuWidth) * -1 - 16px)) scale(.9);
		&::before,
		&::after{
			right: calc(var(--d) * -1);
		}
		>#shield{
			display: block !important;
		}
	}
	a.p-12{
		border-radius: 8px;
		box-shadow: inset 0 9px 0 -8px rgba(var(--textRGB),.075);
		cursor: pointer;
		transition:
			color .5s,
			box-shadow 2s;
		&.active{
			@include text-color($gray-0250);
			box-shadow: none;
		}
	}
	.highlight{
		@include text-color($gray-0250);
		border-radius: 8px;
		//background-image: linear-gradient(to right,$accent-0000,$accent-1000);
		//background-image: radial-gradient(at top,$accent-0000,$accent-1000);
		background-image: linear-gradient(to bottom,$accent-0000,$accent-0500);
		box-shadow: 0 10px 40px rgba($accent-0250,.05);
	}
	.profile-icon{
		border-radius: 4px;
		width: 72px;
		height: 72px;
	}
}